.App {
  text-align: center;
}
body{
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(90deg, rgb(156, 2, 2) 0%, rgb(216, 216, 0) 50%, rgb(1, 87, 1) 100%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.texto-principal h1{
  font-family: sans-serif;
  font-size: 25px;
  color: #fff;
  text-align: center;
  line-height: 1.6;
  text-shadow: 0px 0px 010px black;
}
.texto-principal h2{
  font-family: sans-serif;
  font-size: 22px;
  color: #fff;
  text-align: center;
  line-height: 1.6;
  text-shadow: 0px 0px 010px black;
}
.aside-sub{
  background: #f7f7f7;
  margin: 0;
  width: auto;
  height: auto;
  display: flex;
  padding:20px;
  justify-content: center;   
}
.aside-sub img{
  width: 100%;
}
h4{
  background: #000000;
  color: #fff;
  padding: 10px;
  text-transform: uppercase;
}
  .publicidad-sub{
      background: #f7f7f7;
      width:60%;
      height: auto;
  }
  .publicidad-video{
      background: #f7f7f7;
      width:50%;
      height: 380px;
      padding: 0;
  }
  b{
      color: #ffffff;
      text-shadow: 0px 0px 30px black;
  }
.aside-form{
  background: #f7f7f7;
  width:100%;
  height: auto;
  display:flex;
  padding:0;
  margin:0;	
  justify-content: center;
  align-items: center;
}
form{
    display: flex;
    flex-direction: column;
}
label{
    margin-bottom: 8px;
}
input, textarea{
    margin-bottom: 16px;
    padding: 8px;
}
.boton{
    background: #000;
    color: #fff;
    font-size: 20px;
}
.boton:hover{
    background: #444;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
